import styled from "@emotion/styled";
import { Button, Dropdown, Menu } from "antd";
import { ButtonNoPadding, Row } from "components/lib";
import { ReactComponent as Logo } from "assets/logo.svg";
import { useAuth } from "context/auth-context";
import RouteView from "routers/route-view";
import { useNavigate } from "react-router";
import { useEffect } from "react";
import { ProjectPopover } from "components/project-popover";
import { ProjectModal } from "screens/project-list/project-modal";

export default function MainScreen() {
  const { user } = useAuth();
  const navigate = useNavigate();
  useEffect(() => {
    if (!user) {
      navigate(`/login`);
    }
    return () => {};
  }, [navigate, user]);
  return (
    <Container>
      <PageHeader />
      <Main>
        <RouteView />
      </Main>
      <ProjectModal />
    </Container>
  );
}

const PageHeader = () => {
  const { logout, user } = useAuth();
  const navigate = useNavigate();

  const doLogout = () => {
    logout();
    navigate("/login");
  };

  return (
    <Header between={true}>
      <HeaderLeft gap={true}>
        <ButtonNoPadding type="link" onClick={() => navigate("/projectlist")}>
          <Logo width={`8rem`} height={`3rem`} />
        </ButtonNoPadding>
        <ProjectPopover />
        <span>用户</span>
      </HeaderLeft>
      <HeaderRight gap={true}>
        <Dropdown
          overlay={
            <Menu>
              <Menu.Item>
                <Button type={"link"} onClick={doLogout}>
                  Logout
                </Button>
              </Menu.Item>
            </Menu>
          }
        >
          <Button type={"link"} onClick={(e) => e.preventDefault()}>
            Hi, {user?.name}
          </Button>
        </Dropdown>
      </HeaderRight>
    </Header>
  );
};

// const 和 let 会被提升到 temporal dead zone (暂时性死区); 但在初始化之前使用会报错。var 会变量提升。
const Container = styled.div`
  display: grid;
  grid-template-rows: 6rem 1fr 6rem;
  grid-template-columns: 5rem 1fr 5rem;
  grid-template-areas:
    "header header header"
    "nav main aside"
    "footer footer footer";
  grid-gap: 2rem;
  height: 100vh;
`;

const Header = styled(Row)`
  grid-area: header;
  background: #727272;
`;

const HeaderLeft = styled(Row)``;
const HeaderRight = styled(Row)``;

const Main = styled.main`
  grid-area: main;
  display: flex;
  overflow: hidden;
`;
